import React, { Component } from 'react';
import { List } from 'antd';
import { Link } from "react-router-dom";
import { gojsData } from "../../assets/data";
import "./index.less";

export default class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: []
        };
    }

    componentDidMount() {
        let dataSource = this.state.dataSource;
        for (let i = 0; i < 1; i++) {
            dataSource.push({
                href: gojsData.baseHref,
                className: gojsData.name,
                title: gojsData.name,
                description: gojsData.description,
                content: gojsData.content,
            });
        }
        this.setState({
            dataSource
        });
    }
    render() {
        return (
            <List
                itemLayout="vertical"
                size="large"
                dataSource={this.state.dataSource}
                renderItem={item => (
                    <Link to={item.href}>
                        <List.Item
                            className={item.className}
                            key={item.title}
                            extra={
                                <div className="box">
                                    <img src="https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png" alt={item.title} />
                                    <div className="box-content">
                                        <h3 className="title">点击进入教程</h3>
                                    </div>
                                </div >

                            }
                        >
                            <List.Item.Meta
                                title={item.title}
                                description={item.description}
                            />
                            <p style={{ textIndent: "2em", color: "rgba(0, 0, 0, 0.65)" }}>{item.content}</p>
                        </List.Item>
                    </ Link>
                )}
            />
        )
    }
}
